import React from 'react';
import { Jumbotron as Jumbo, Container, Row, Col, Button } from 'react-bootstrap';
import styled from 'styled-components';
import resImage from "../assets/bigsplash.png";
import LineHRclean from './LineHRclean';
import { Link as ScrollLink, Element} from 'react-scroll';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

const Styles = styled.div`
  .jumbo{

      background: url(${resImage});
      color: #efefef;
      position: relative;


      height: 100vh;



      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;

    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    }

    a{
      color: #bbb !important;
      &:hover
      {
        color: white !important;
        text-decoration: none;
      }
    }


`;

function Jumbotron(){
  return(
    <Styles>
      <Jumbo fluid className="jumbo">
      <div className="overlay"></div>
      <Container  className="d-flex justify-content-center align-items-center">


            <Row>
              <Col>
                <h1>Javier van Wyk</h1>
                <h4>Software Engineer</h4>
                <LineHRclean />




                  <h2>
                    <a href="https://github.com/JavierVW" target="_blank" rel="noopener noreferrer">  <FontAwesomeIcon icon={['fab', 'github']}/></a>
                    <a href="https://www.linkedin.com/in/javier-van-wyk-85777b186/" target="_blank" rel="noopener noreferrer">  <FontAwesomeIcon icon={['fab', 'linkedin']}/></a>
                  </h2>


              </Col>

            </Row>

      </Container>
      </Jumbo>
    </Styles>

  );
}

export default Jumbotron;
