import React from 'react';
import { Nav, Navbar, Container } from 'react-bootstrap';
import styled from 'styled-components';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

const Styles = styled.div`
  .navbar{
    background-color: #222 !important;
  }
  a, .navbar-brand, .nav-link{
color: #bbb !important;
    &:hover
    {
      color: white !important;
      text-decoration: none !important;
    }
  }
  .navbar-nav, .navbar-text{
  color: #bbb !important;
  text-decoration: none !important;
  }
`;



function NavFooter(){
  return(
    <Styles>
      <Navbar expand="lg">
        <Container>

          <Nav className="flex-column mr-auto">
            <Navbar.Text>
              <h5>JAVIER VAN WYK</h5>
              </Navbar.Text>
              <Nav.Item>
                Software Engineer
                </Nav.Item>
                <Nav.Item>
                  <br></br>
                  </Nav.Item>
                  <Nav.Item>
                    <br></br>
                    </Nav.Item>

          </Nav>

        <Nav className="flex-column ml-auto">
          <Navbar.Text>
            <h5>LINKS</h5>
            </Navbar.Text>
            <br/>
          <Nav.Item as='li'><a href="https://github.com/JavierVW" target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={['fab', 'github']}/> Github</a></Nav.Item>
          <Nav.Item as='li'><a href="https://www.linkedin.com/in/javier-van-wyk-85777b186/" target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={['fab', 'linkedin']}/> LinkedIn</a></Nav.Item>

            <Nav.Item>
              <br></br>
              </Nav.Item>
        </Nav>

        </Container>
      </Navbar>
    </Styles>
  )
}

export default NavFooter;
