import React from 'react';
import { Card, Button, Col } from 'react-bootstrap';

function Cards(props){
  return(


      <Col lg={4}>
        <Card style={{ width: '18rem' }} className="mb-4" border="secondary" bg="light">
          <Card.Img variant="top" src={props.image} alt={props.name} />
          <Card.Body>
            <Card.Title>{props.name}</Card.Title>
            <Card.Text>
              {props.description}
            </Card.Text>
            <Card.Text>
              {props.description2}
            </Card.Text>
            <Button variant="lux" href={props.git}>GitHub / Link</Button>
          </Card.Body>
        </Card>
      </Col>



  );
}

export default Cards;
