import React from 'react';
import { Nav, Navbar, Container } from 'react-bootstrap';
import styled from 'styled-components';
import { HashLink } from 'react-router-hash-link';
import brandImage from "../assets/jvw_white.svg";

const Styles = styled.div`
  .navbar{
    background-color: #07070A !important;
  }
  a, .navbar-brand, .navbar-nav, .nav-link{
    color: #bbb !important;
    &:hover
    {
      color: white !important;
      text-decoration: none;
    }
  }

`;

function NavBar(){
  return(
    <Styles>
      <Navbar expand="lg fixed-top">
        <Container>
        <Navbar.Brand href="#" smooth>
          <img
                  src={brandImage}
                  height="30"
                  className="d-inline-block align-top"
                  alt="JVW"
                />
        </Navbar.Brand>


        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto">
            <Nav.Item><Nav.Link> <HashLink to="/#projectsID" smooth>Projects</HashLink></Nav.Link></Nav.Item>
            <Nav.Item><Nav.Link> <HashLink to="/#aboutSection" smooth>About</HashLink></Nav.Link></Nav.Item>
          </Nav>
        </Navbar.Collapse>
      </Container>
      </Navbar>
    </Styles>
  )
}

export default NavBar;
