import React from 'react';
import { HashRouter as Router, Route, Switch} from 'react-router-dom';
import Home from './Home';
import About from './About';
import Contact from './Contact';
import Projects from './Projects';
import NoMatch from './NoMatch';
import NavBar from './components/NavBar';
import NavFooter from './components/NavFooter';
import './styling.css'
import { library } from '@fortawesome/fontawesome-svg-core'
import { fab, faTwitter, faJava, faAndroid, faHtml5, faCss3, faReact, faLinux, faAdobe, faGithub, faLinkedin } from '@fortawesome/free-brands-svg-icons'
import { faCheckSquare, faCoffee, faAtom, faTerminal } from '@fortawesome/free-solid-svg-icons'

library.add(fab, faCheckSquare, faCoffee, faTwitter, faAtom, faLinux, faTerminal, faAdobe, faGithub, faLinkedin)


function App() {

  return (
    <React.Fragment>
        <Router>

          <NavBar />

            <Switch>
              <Route exact path="/" component={Home} />
              <Route path="/about" component={About} />

              <Route component={NoMatch} />
            </Switch>
            <NavFooter />
        </Router>
    </React.Fragment>
  );
}

export default App;
