import React from 'react';
import styled from 'styled-components';

function LineHRclean(){

  const Styles = styled.div`
    hr{
      width: 500px;
      border-top: 1px solid;
      border-bottom: 1px solid;
    }

  `;

  return(
    <Styles>
      <hr />
    </Styles>
  );
}

export default LineHRclean;
