import React from 'react';
import Jumbotron from "./components/Jumbotron";

import Projects from './Projects';
import ToolsSkills from './ToolsSkills';
import { Container, Row, Col } from 'react-bootstrap';
import LineHR from './components/LineHR';
import { HashLink } from 'react-router-hash-link';

let welcome = "About Me";
let infoText = "I am a software developer open to front end, back end and mobile development. I obtained a BSc in Computer Science from the University of the Western Cape."
function Home(){


  return(
    <React.Fragment>
      <Jumbotron id="homeID"/>
        <LineHR />
          <Container id="projectsID">
            <Projects />
          </Container>
        <LineHR />

      <Container className="d-flex justify-content-center align-items-center">
        <Row>
          <Col>
            <h2>{welcome}</h2>
            <p>
              {infoText}
            </p>
            <p>
              Information about some of the university courses I took, specifically the computer science classes, can be found <HashLink to="/about" rel="noopener noreferrer">here</HashLink>
            </p>
          </Col>

        </Row>

      </Container>
      <LineHR />

      <Container id="menuMini">
        <ToolsSkills />

      </Container>


      <Container id="aboutSection">

      </Container>



      <LineHR />



    </React.Fragment>
  );
}

export default Home;
