import React from 'react';
import styled from 'styled-components';

function LineHR(){

  const Styles = styled.div`
    hr{
      width: 500px;
      border-top: 1px solid;
      border-bottom: 1px solid;
    }

  `;

  return(
    <Styles>
      <br/>
      <hr />
      <br/>
    </Styles>
  );
}

export default LineHR;
