import React from 'react';
import { Row, Col, Container } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'


function ToolsSkills(){


  return(
<React.Fragment >
    <Container className="d-flex justify-content-center align-items-center">
      <Row>
        <Col>
          <h2>Tools and Skills</h2>

        </Col>

      </Row>

    </Container>

      <Container >

        <Row>

          <Col>
            <h2>Skills</h2>

              <ul className="list-unstyled">
                       <li><FontAwesomeIcon icon={['fab', 'java']} fixedWidth/> Java</li>
                       <li><FontAwesomeIcon icon={['fab', 'python']} fixedWidth/> Python</li>
                       <li><FontAwesomeIcon icon={['fab', 'android']} fixedWidth/> Android development</li>
                       <li><FontAwesomeIcon icon={['fab', 'html5']} fixedWidth/> HTML5</li>
                       <li><FontAwesomeIcon icon={['fab', 'css3']} fixedWidth/> CSS3 and Bootstrap4</li>
                       <li><FontAwesomeIcon icon={['fab', 'react']} fixedWidth/> ReactJS</li>
                    </ul>
          </Col>

          <Col>
          </Col>

          <Col>
            <h2>Tools</h2>

              <ul className="list-unstyled">
                       <li><FontAwesomeIcon icon={['fab', 'java']} fixedWidth/> IntelliJ IDEA</li>
                       <li><FontAwesomeIcon icon={['fab', 'android']} fixedWidth/> Android Studio</li>
                       <li><FontAwesomeIcon icon={['fab', 'python']} fixedWidth/> PyCharm</li>
                       <li><FontAwesomeIcon icon={"atom"} fixedWidth/> Atom</li>
                       <li><FontAwesomeIcon icon={['fab', 'python']} fixedWidth/> Jupyter Notebook</li>
                       <li><FontAwesomeIcon icon={['fab', 'linux']} fixedWidth/> Linux</li>
                       <li><FontAwesomeIcon icon={'terminal'} fixedWidth/> Command Line</li>
                       <li><FontAwesomeIcon icon={['fab', 'adobe']} fixedWidth/> Adobe XD</li>
                    </ul>

          </Col>
        </Row>


      </Container>
</React.Fragment>
  );
}

export default ToolsSkills;
