import React from 'react';
import { Row, Col, Container, CardDeck, Card } from 'react-bootstrap';
import Cards from "./components/Cards";
import GitDetails from './GitDetails';
import resImage from "./assets/clowdy2.png";

function Projects(){
  return(



    <React.Fragment >
      <Container className="d-flex justify-content-center align-items-center">
        <Row>
          <Col>
            <h2>Projects</h2>

          </Col>

        </Row>

      </Container>

      <Container className="d-flex justify-content-center align-items-center">
        <Row>
          <Col>
            <p>
              A collection of some of my projects along with Github links
            </p>
          </Col>

        </Row>

      </Container>
      <br/>
      <CardDeck>

          {GitDetails.map((cardDetails) => {
            return(

              <Cards
              key={cardDetails.id}
              name={cardDetails.name}
              description={cardDetails.description}
              description2={cardDetails.description2}
              git={cardDetails.git}
              image={cardDetails.image}
              />


            )
          })}

          <Col lg={4}>
            <Card style={{ width: '18rem' }} className="mb-4" border="secondary" bg="light">
              <Card.Img variant="top" src={resImage} alt="Clowdy" />
              <Card.Body>
                <Card.Title>Clowdy</Card.Title>
                <Card.Text>
                  An online music player which lets the user listen to their own music directly from the cloud.
                </Card.Text>
                <Card.Text>
                  Work in Progress.
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>

      </CardDeck>

    </React.Fragment>
  );
}

export default Projects;
